  padding-left: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }

.nav__item + .nav__item {
  margin-left: 16.5%; }

.nav__item:hover a,
.nav__item:focus a {
  opacity: 1; }

.nav__item a {
  font-size: 16px;
  font-weight: 300;
  line-height: 32px;
  color: #ffffff;
  letter-spacing: -0.005em;
  opacity: 0.7; }

.nav__toggle {
  display: none;
  z-index: 10; }

@media (max-width: 1023px) {
  .nav__list {
    display: none;
    flex-direction: column;
    padding: 0;
    padding-top: 100px;
    padding-bottom: 140px; }
  .nav--opened .nav__list-wrapper {
    height: 100vh;
    position: fixed;
    width: 100%;
    overflow-y: scroll;
    z-index: 5;
    background-color: #ffffff; }
  .nav--opened .nav__list {
    display: block;
    width: 100%;
    background-color: #ffffff; }
  .nav--opened .nav__item,
  .nav--nojs .nav__item {
    margin-left: 0; }
  .nav--nojs .nav__list {
    display: block; }
  .nav__item a {
    display: block;
    position: relative;
    padding-top: 17px;
    padding-bottom: 17px;
    padding-left: 16px;
    margin-bottom: 28px;
    background-color: #f6f6f6;
    font-size: 20px;
    line-height: 28px;
    color: #48494d; }
  .nav__item:last-child a {
    margin-bottom: 0; }
  .nav__item a::after {
    content: "";
    position: absolute;
    width: 25px;
    height: 25px;
    top: 50%;
    right: 15px;
    transform: translateY(-50%);
    background-image: url("../img/icon-arrow.svg");
    background-repeat: no-repeat;
    background-position: center; }
  .nav__toggle {
    display: block;
    position: absolute;
    right: 0;
    padding: 13px;
    background-color: #0ad9c6;
    border: none;
    outline: none;
    cursor: pointer; }
    .nav__toggle::before, .nav__toggle::after {
      content: "";
      display: block;
      width: 23px;
      height: 2px;
      margin-bottom: 4px;
      background: #000000;
      transition: transform 0.3s; }
    .nav__toggle::after {
      margin-bottom: 0; }
    .nav__toggle:focus, .nav__toggle:hover {
      background-color: #06776d; }
  .nav__toggle-line {
    display: block;
    width: 23px;
    height: 2px;
    margin-bottom: 4px;
    background: #000000;
    opacity: 1;
    transition: 0.8s; }
  .nav--opened .nav__toggle::after,
  .nav--nojs .nav__toggle::after {
    width: 23px;
    transform: translateY(-6px) rotate(-135deg); }
  .nav--opened .nav__toggle::before,
  .nav--nojs .nav__toggle::before {
    transform: translateY(6px) rotate(135deg); }
  .nav--opened .nav__toggle-line,
  .nav--nojs .nav__toggle-line {
    opacity: 0;
    transition: 0s; }
    .nav--opened .nav__toggle-line::after,
    .nav--nojs .nav__toggle-line::after {
      width: 23px;
      transform: translateY(-6px) rotate(-135deg); }
    .nav--opened .nav__toggle-line::before,
    .nav--nojs .nav__toggle-line::before {
      transform: translateY(6px) rotate(135deg); } }

