.contacts {
  max-width: 1025px;
  padding: 0 35px;
  margin: 0 auto;
  margin-bottom: 95px;
  text-align: center;
}

.contacts__title {
  margin: 0;
  margin-bottom: 28px;
  font-size: 30px;
  line-height: 30px;
  letter-spacing: 0.005em;
  font-weight: 600;
  color: #1f1f1f;
}

.contacts__address {
  width: 57%;
  margin: 0 auto;
  margin-bottom: 40px;
  font-size: 17px;
  line-height: 31px;
  letter-spacing: 0.005em;
  font-style: normal;
  opacity: 0.7;
}

.contacts__map {
  position: relative;
  margin-bottom: 35px;
  max-width: 100%;
  height: 530px;
}

.contacts__map img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

.contacts__links {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 59%;
  margin: 0 auto;
}

.contacts__links-item {
  position: relative;
  margin-bottom: 20px;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: 0.005em;
  color: #000000;
  opacity: 0.5;

  &:hover,
  &:focus {
    opacity: 1;
  }
}

.contacts__links-item--mail {
  padding-left: 41px;
}

.contacts__links-item--phone {
  margin-right: 16px;
  padding-left: 37px;
    letter-spacing: 0.03em;

}

.contacts__links-item::before {
  position: absolute;
  content: "";
}

.contacts__links-item--mail::before {
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 26px;
  height: 20px;
  background-image: url("../img/icon-email.svg");
  background-repeat: no-repeat;
}

.contacts__links-item--phone::before {
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 21px;
  height: 23px;
  background-image: url("../img/icon-phone.svg");
  background-repeat: no-repeat;
}

@include media-tablet {
  .contacts__title {
    margin-bottom: 20px;
    font-size: 24px;
  }

  .contacts__map {
    margin-bottom: 36px;
  }

  .contacts__address {
    margin-bottom: 20px;
    width: 100%;
  }

  .contacts__links {
    width: 80%;
  }
}

@include media-mobile {
  .contacts {
    padding: 0;
  }

  .contacts__title {
    font-size: 18px;
  }

  .contacts__address {
    padding: 0 25px;
    box-sizing: border-box;
    width: 100%;
  }

  .contacts__map {
    height: 250px;
    margin-bottom: 30px;
  }

  .contacts__links {
    flex-direction: column;
    align-items: center;
  }

  .contacts__links-item--mail {
    margin-bottom: 23px;
  }
}
